<template>
<div>
    <div id="particles_con"></div>
    <img src="../../assets/logo.png" class="logo" />
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>GM后台管理平台</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="handleChangeStatus">{{isLogin?'注册':'登录'}}</el-button>
        </div>
        <div style="textAlign:center;">
            <div v-show="isLogin">
                <el-form :model="ruleFormlogin" status-icon :rules="ruleLogin" ref="ruleFormlogin" label-width="80px">
                    <el-form-item label="用户名" prop="account">
                        <el-input v-model="ruleFormlogin.account"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="ruleFormlogin.password" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item style="textAlign:right;">
                        <el-button type="primary" @click="submitFormLogin('ruleFormlogin')">登录</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div v-show="!isLogin">
                <el-form :model="ruleFormRegist" status-icon :rules="ruleRegist" ref="ruleFormRegist" label-width="80px">
                    <el-form-item label="用户名" prop="account">
                        <el-input v-model="ruleFormRegist.account"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="ruleFormRegist.password" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="checkPass2">
                        <el-input type="password" v-model="ruleFormRegist.checkPass2" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="ruleFormRegist.name"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="ruleFormRegist.email"></el-input>
                    </el-form-item>
                    <el-form-item style="textAlign:right;">
                        <el-button type="primary" @click="submitFormRegist('ruleFormRegist')">注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </el-card>
</div>
</template>

<script>
import md5 from "js-md5";
import "particles.js/particles.js";
import particlesOptions from "@/libs/particles";

export default {
    name: "login",
    data() {
        let validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error("请输入密码"));
            } else {
                if (this.ruleFormRegist.checkPass2) {
                    this.$refs.ruleFormRegist.validateField("checkPass2");
                }
                callback();
            }
        };
        let validatePassCheck = (rule, value, callback) => {
            if (value === "") {
                callback(new Error("请再次输入密码"));
            } else if (value !== this.ruleFormRegist.password) {
                callback(new Error("两次输入密码不一致!"));
            } else {
                callback();
            }
        };
        return {
            isLogin: true,
            ruleFormlogin: {},
            ruleFormRegist: {},
            ruleLogin: {
                account: [{
                        required: true,
                        message: "不能为空！",
                        trigger: "blur"
                    },
                    {
                        min: 4,
                        max: 20,
                        message: "长度在 4 到 20 个字符",
                        trigger: "blur"
                    }
                ],
                password: [{
                        required: true,
                        message: "不能为空！",
                        trigger: "blur"
                    },
                    {
                        min: 4,
                        max: 20,
                        message: "长度在 4 到 20 个字符",
                        trigger: "blur"
                    }
                ]
            },
            ruleRegist: {
                account: [{
                        required: true,
                        message: "不能为空！",
                        trigger: "blur"
                    },
                    {
                        min: 4,
                        max: 20,
                        message: "长度在 4 到 20 个字符",
                        trigger: "blur"
                    }
                ],
                password: [{
                        validator: validatePass,
                        required: true,
                        trigger: "blur"
                    },
                    {
                        min: 4,
                        max: 20,
                        message: "长度在 4 到 20 个字符",
                        trigger: "blur"
                    }
                ],
                checkPass2: [{
                        validator: validatePassCheck,
                        required: true,
                        trigger: "blur"
                    },
                    {
                        min: 4,
                        max: 20,
                        message: "长度在 4 到 20 个字符",
                        trigger: "blur"
                    }
                ],
                name: [{
                    required: true,
                    message: "不能为空！",
                    trigger: "blur"
                }],
                email: [{
                        required: true,
                        message: "不能为空！",
                        trigger: "blur"
                    },
                    {
                        type: "email",
                        message: "请输入正确的邮箱地址",
                        trigger: ["blur", "change"]
                    }
                ]
            }
        };
    },
    methods: {
        submitFormRegist(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.ruleFormRegist));
                    delete objCopy.checkPass2;
                    objCopy.password = md5(objCopy.password);
                    this.$fetch.post(API.app.regist, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.isLogin = true;
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        submitFormLogin(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.ruleFormlogin));
                    objCopy.password = md5(objCopy.password);
                    this.$fetch.post(API.app.login, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            let userInfo = res.data;
                            sessionStorage.setItem("status", Date.parse(new Date()));
                            sessionStorage.setItem("userInfo", JSON.stringify(userInfo));
                            this.$refs[formName].resetFields();
                            this.$router.push({
                                name: "role"
                            });
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        handleChangeStatus() {
            this.isLogin = !this.isLogin;
        }
    },
    computed: {},
    mounted() {
        particlesJS("particles_con", particlesOptions);
    }
};
</script>

